<template>
<div>
    <div>
        <b-modal id="finishTypingModal" hide-footer @hidden="resetValues()">
            <template v-slot:modal-title>
                Your Result
            </template>
            <div class="d-block text-center">
                <h3>WPM: {{correct}}</h3>   
            </div>
            <b-button class="mt-3" block @click="$bvModal.hide('finishTypingModal')">Close</b-button>
        </b-modal>
    </div>
    <div class="timer-container">
        <span ref="timerCount" class="timer-count timer-50 noselect">{{timer}} {{timer > 1 ? "Seconds" : "Second"}}</span>
    </div>
    <div class="rand-text" ref="rand-text">
        <span class="single-char noselect" v-for="(word, index) in arrayOfWordsWithSpace" v-bind:key="word+index" v-bind:ref="word + index">{{word}}</span>
    </div>
    <div class="input-container">
        <h1>Type As Fast As You Can</h1>
        <input type="text" class="type-random-text" ref="userInput" @keyup.delete="checkInput($event)" @keydown.delete="checkInput($event)" @keydown="checkInput($event)" :maxlength="stringLength.length" placeholder="Start Typing Here...">
        <div class="correct-error-count">
            <span class="correct">Correct: {{correct}}</span>
            <br>
            <span class="error">Error: {{errors}}</span>
        </div>
        <div class="generate-new-text-container">
            <button class="generate-new-text" v-on:click="getRandomText()">თავიდან ცდა</button>
        </div>
    </div>
</div>
</template>
<script>
</script>

<script>
    import * as tdata from "../assets/data.json";

export default {
    name: 'TypingSpeed',
    data: () => {
        return {
            arrayOfWordsWithSpace: [],
            arrayOfWords: [],
            userInput: '',
            Success: false,
            errors: 0,
            correct: 0,
            userInputPrevValue: [],
            marginLeft: 0,
            timer: 60,
            timerCountInterval: Function,
            timerRunning: false,
            stringLength: 0
        }
    },
    methods: {
        checkInput(event){
            if(this.timerRunning === false) {
                this.timerCount();
            }
            if(this.correct + this.errors >= 100 && event.keyCode === 32) {
                alert('No More Words To Type');
                return 0;
            }
           if(event.keyCode === 8 || event.keyCode === 32) {
                let value = event.target.value;  
            let inputValueArray = value.split(' '); 
            if(inputValueArray[inputValueArray.length - 1] === ' ') {
                inputValueArray.pop();
            }
            let lastWordIndex = inputValueArray.length - 1;

                if(event.keyCode === 8) {
                    if(inputValueArray[inputValueArray.length - 1] === '') {
                        inputValueArray.pop();
                        if(inputValueArray.length === 0) {
                            this.arrayOfWordsWithSpace.forEach((el, i) => {
                                let $ref = this.$refs[this.arrayOfWordsWithSpace[i] + (i).toString()];
                                if(this.errors > 0 && $ref[0].classList.contains('incorrect-word')) {
                                    this.errors--;
                                    $ref[0].classList.remove('incorrect-word')
                                }

                                if(this.correct > 0 && $ref[0].classList.contains('correct-word')) {
                                    this.correct--;
                                    $ref[0].classList.remove('correct-word')
                                }

                                if($ref[0].classList.contains('current-word') && i > 0) {
                                    $ref[0].classList.remove('current-word')
                                } else if( i === 0) {
                                    $ref[0].classList.add('current-word')
                                }
                            })
                        return; 
                    }
                }
                if(inputValueArray.length < this.userInputPrevValue.length || inputValueArray[inputValueArray.length - 1].length - 1 < this.userInputPrevValue[this.userInputPrevValue.length - 1].length) {
                    let refIndex = lastWordIndex * 2;

                    let $ref = this.$refs[this.arrayOfWordsWithSpace[refIndex] + (refIndex).toString()];
                    if(this.errors > 0 && $ref &&  $ref[0].classList.contains('incorrect-word')) {
                        this.errors--;
                            $ref[0].classList.remove('incorrect-word')
                            $ref[0].classList.add('current-word')

                    }
                    if(this.correct > 0 && $ref[0].classList.contains('correct-word')) {
                        this.correct--;
                            $ref[0].classList.remove('correct-word')
                            $ref[0].classList.add('current-word')

                    }
                    if(this.correct > 0 && $ref[0].classList.contains('correct-word')) {
                        this.correct--;
                        $ref[0].classList.remove('correct-word')

                    }
                    refIndex = (lastWordIndex + 1) * 2;
                    $ref =  this.$refs[this.arrayOfWordsWithSpace[refIndex] + (refIndex).toString()];
                    if($ref)
                        $ref[0].classList.remove('current-word');
                    this.userInputPrevValue = inputValueArray;
                    return;
                }
            }

            if(event.keyCode === 8) {
                return;
            }
            value = value.trim().split(' ')[value.trim().split(' ').length - 1];
            let refIndex = lastWordIndex * 2;

            let $ref = this.$refs[this.arrayOfWordsWithSpace[refIndex] + (refIndex).toString()];
            let stringWord = this.arrayOfWords[lastWordIndex];
            let valueWord = value;

            if(stringWord === valueWord) {
                if($ref && $ref.length >= 0) {
                    $ref[0].classList.remove('current-word');
                    $ref[0].classList.add('correct-word')
                }
                this.correct++;
            } else {
                    if($ref && $ref.length >= 0) {
                    $ref[0].classList.remove('current-word');
                    $ref[0].classList.add('incorrect-word')
                    }
                    this.errors++;
                }
            this.userInputPrevValue = inputValueArray;
            refIndex = (lastWordIndex + 1) * 2;
            $ref = this.$refs[this.arrayOfWordsWithSpace[refIndex] + (refIndex).toString()];
            if($ref)
                $ref[0].classList.add('current-word');
           }
        },

        timerCount() {
            this.timerRunning = true;
             this.timerCountInterval = setInterval(() => {
                if(this.timer <= 10 && this.timer > 5) {
                    this.$refs['timerCount'].classList.remove('timer-50')
                    this.$refs['timerCount'].classList.add('timer-55')
                } else if (this.timer <= 5 && this.timer > 0) {
                    this.$refs['timerCount'].classList.remove('timer-55')
                    this.$refs['timerCount'].classList.add('timer-60')
                }else 
                if(this.timer === 0) {
                    this.$bvModal.show('finishTypingModal');
                    this.timer = 0;
                    this.$refs['timerCount'].classList.remove('timer-60')
                    this.$refs['timerCount'].classList.add('timer-50')
                    this.clearTimeOut();
                    return;
                }
                this.timer--;
            }, 1000);
        },

        clearTimeOut: function() {
            clearInterval(this.timerCountInterval);
            this.timer = 60;
            this.timerRunning = false;

        },

        resetValues() {
            this.getRandomText();
        },

        getRandomText(){
            this.clearTimeOut();
            this.correct = 0;
            this.errors = 0;
            this.userInputPrevValue = [];
            this.arrayOfWords = [];
            if(this.$refs['userInput']) {
                this.$refs['userInput'].value = '';
            }

            let randTextData = tdata.data.split(' ');
            this.stringLength = tdata.data.length;
            let dataLength = randTextData.length;
            let randText = [];

            for(let i = 0; i < 100; i++) {
                randText.push(randTextData[Math.floor(Math.random() * dataLength)]);
                if(i != 100){
                    randText.push(' ');
                }
            }
            randText.forEach(el => {
                if(el !== ' ') {
                    this.arrayOfWords.push(el);
                }
            })
            this.arrayOfWordsWithSpace = randText;
            setTimeout(() => {
                this.$refs[this.arrayOfWordsWithSpace[0] + (0).toString()][0].classList.add('current-word');
            },0)
        }
    },
    created() {
        this.getRandomText();
    }
}
</script>
<style scoped>
    .input-container {
        max-width: 600px;
        margin: 20px auto;
    }
    .type-random-text {
        width: 100%;
        height: 45px;
        border: 0.5px solid rgba(0, 0, 0, 0.5);
        border-radius: 5px;
        padding: 5px 10px;
        font-size: 16px
    }
    .correct-error-count {
        display: flex;
        justify-content: space-around;
        padding: 1.5rem 9rem;
    }
    .correct-word {
        color: #32a852;
    }
    .incorrect-word {
        color: #ad3e28;
    }
    .current-word {
        color: #abb524;
    }
    .correct {
        display: inline-block;
        font-size: 1.5rem;
        font-weight: 700;
        color: green;
    }

    .error {
        display: inline-block;
        font-size: 1.5rem;
        font-weight: 700;
        color: red;
    }

    .rand-text {
    position: relative;
        padding-left: 10%;
        padding-right: 9.9%;
        padding-bottom: 30px;
        text-align: left;
    }

    .rand-text::before {
        content: '';
        display: inline-block;
        width: 80%;
        height: 1px;
        background: rgba(0, 0, 0, 0.7);
        position: absolute;
        left: 50%;
        transform: translate(-50%, 0);
        bottom: 0

    }

    .generate-new-text {
        border: none;
        padding: 10px 20px;
        color: #fff;
        background: #f7a732;
        font-size: 1.2rem;
        border-radius: 5px;
        cursor: pointer;
    }

    .single-char {
        display: inline-block;
        min-width: 5px;
        font-size: 24px
    }

    .correct-background {
        background: rgba(66, 245, 114, 0.5);
    }
    .timer-container {
        padding: 0 10% 2rem;

    }
    .timer-count {
        font-size: 1.5rem;
        font-weight: 600;
    }

    .timer-50 {
        color: green;
    }

    .timer-55 {
        color: #f7a732;

    }

    .timer-60 {
        color: red;
    }

    .noselect {
        -webkit-touch-callout: none;
        -webkit-user-select: none;
        -khtml-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none; 
        user-select: none;           
    }
</style>